/* stylelint-disable font-family-name-quotes */
/* stylelint-disable font-family-no-missing-generic-family-keyword */
/* stylelint-disable selector-type-no-unknown, selector-max-type */
@import '@antmjs/vantui/es/style/base.less';
page,
body {
  background-color: #f7f8fa;
  font-size: 28px;
}
body taro-button-core {
  width: initial;
}

taro-button-core + taro-button-core {
  margin-top: 0 !important;
}

.demo-margin-left {
  margin-left: 10px;
}

.demo-margin-right {
  margin-right: 10px;
}

.demo-margin-bottom {
  margin-bottom: 15px;
}

@font-face {
  font-family: 'page-icon'; /* Project id 3412886 */

  /* Color fonts */
  src: url('data:font/ttf;charset=utf-8;base64,')
    format('truetype');
}

.pages-icon-index {
  background-color: #ffffff;
  .van-col {
    display: flex;
    align-items: center;
    // justify-content: center;
    flex-direction: column;
  }

  .page-icon {
    font-family: 'page-icon' !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .page-icon-demo1::before {
    content: '\ea3c';
  }

  .page-icon-demo2::before {
    content: '\ea3d';
  }

  .page-icon-demo3::before {
    content: '\ea3e';
  }
}

.pages-button-index {
  .van-button {
    margin-right: 20px;
    margin-bottom: 20px;
  }
}

.pages-checkbox-index {
  .van-checkbox-group {
    .van-checkbox {
      margin-bottom: 26px;
    }
  }
}

.pages-circle-index {
  .van-circle {
    margin-right: 20px;
    margin-bottom: 20px;
  }
}

.pages-form-index {
  .van-button-submit {
    width: 100%;
    margin-top: 20px;
  }
}

.pages-goods-action-index {
  .van-goods-action {
    position: relative !important;
  }
}

.pages-image-index {
  background-color: #ffffff;
  .van-col {
    color: #7d7e80;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
}

.pages-loading-index {
  .van-loading {
    margin-right: 40px;
  }
}

.pages-notify-index {
  .van-button {
    margin-right: 20px;
    margin-bottom: 20px;
  }
}

.pages-overlay-index {
  .van-overlay {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .block {
    width: 200px;
    height: 200px;
    background-color: #ffffff;
  }
}

.pages-popup-index {
  .van-popup {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    text-align: center;
  }
  .van-popup--center {
    width: 200px;
    height: 200px;
  }
  .van-popup--top,
  .van-popup--bottom {
    width: 100%;
    height: 30%;
  }
  .van-popup--left,
  .van-popup--right {
    width: 30%;
    height: 100%;
  }
}

.pages-progress-index {
  .van-progress {
    margin-bottom: 50px;
  }
}

.pages-sidebar-index {
  background-color: #ffffff;

  .demo-block {
    width: 20%;
    float: left;
    margin-left: 100px;
  }
}

.pages-slider-index {
  .demo-block {
    margin-bottom: 40px;
  }
  .customButton {
    width: 112px;
    color: #ffffff;
    font-size: 20px;
    line-height: 36px;
    text-align: center;
    border-radius: 200px;
    background-color: #ee0a24;
    visibility: visible;
  }
}

.pages-stepper-index {
  background-color: #ffffff;

  .demo-block {
    padding-bottom: 20px;
    border-bottom: 1px solid #ebedf0;

    &:last-child {
      border: none;
    }
  }
}

.pages-sticky-index {
  position: relative;
  .demo-block:last-child {
    padding-bottom: 1800px !important;
  }
}

.pages-submit-bar-index {
  .van-submit-bar {
    position: relative !important;
  }
}

.pages-swipe-cell-index {
  .van-swipe-cell__right,
  .van-swipe-cell__left {
    display: flex;
    align-items: center;
  }
}

.pages-tag-index {
  .van-tag {
    margin-right: 20px;
  }
}

.pages-tabbar-index {
  .van-tabbar {
    position: relative !important;
  }
}

.pages-toast-index {
  .van-button {
    margin-right: 20px;
  }
}

.pages-transition-index {
  .center-x {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    margin: -100px 0 0 -100px;
    background-color: #1989fa !important;
  }

  .van-enter-active-class,
  .van-leave-active-class {
    transition-property: background-color, transform;
  }

  .van-enter-class,
  .van-leave-to-class {
    background-color: rgb(186, 92, 92);
    transform: rotate(-360deg) translate3d(-100%, -100%, 0);
  }
}

.pages-uploader-index {
  background-color: #ffffff;
}

.pages-radio-index {
  .van-radio--vertical {
    margin-bottom: 20px;
  }
}

.pages-index-bar-index {
  .van-button {
    margin-left: 40px;
    margin-top: 40px;
  }
}

.pages-col-index {
  .dark,
  .light {
    color: #ffffff;
    font-size: 26px;
    line-height: 60px;
    text-align: center;
    margin-bottom: 20px;
    background-clip: content-box;
  }

  .dark {
    background-color: #39a9ed;
  }

  .light {
    background-color: #66c6f2;
  }
}

.pages-power-scroll-view-index {
  // 自适应scroll布局
  .scroll-container {
    display: flex;
    flex-direction: column;
    .scroll-container-content {
      flex: 1;
      overflow: scroll;
      & > scroll-view,
      & > taro-scroll-view-core {
        height: 100%;
      }
    }
  }

  .footer {
    line-height: 50px;
    font-size: 30px;
    background-color: #90ee90;
    color: #ffffff;
    text-align: center;
    padding-bottom: constant(safe-area-inset-bottom); /* 兼容 iOS < 11.2 */
    /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
    padding-bottom: env(safe-area-inset-bottom); /* 兼容 iOS >= 11.2 */
  }

  .placeholder {
    padding-top: 30vh;
    text-align: center;
    color: #cccccc;
    .loadingWrapper {
      font-size: 24px;
      margin-bottom: 24px;
    }
  }

  .header {
    padding: 12px;
    background: #ffffff;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    left: 0;
    .left {
      flex: auto;
    }
    .right {
      flex: none;
      padding-left: 12px;
    }
  }
}

